import React, { useState, useEffect, useCallback } from 'react';
import { BrowserRouter as Router, Routes, Route, Link } from 'react-router-dom';
import { QueryClient, QueryClientProvider } from '@tanstack/react-query';
import { ReactQueryDevtools } from '@tanstack/react-query-devtools';
import SearchPage from './pages/SearchPage';
import TrackedList from './pages/TrackedList';
import BackgroundGradient from './components/BackgroundGradient';
import apiService from './services/api';
import './App.css';

const BOT_USERNAME = process.env.REACT_APP_TELEGRAM_BOT_USERNAME || '';

// React Query Client
const queryClient = new QueryClient({
  defaultOptions: {
    queries: {
      staleTime: 5 * 60 * 1000, // 5 
      cacheTime: 10 * 60 * 1000, // 10 
      refetchOnWindowFocus: false,
      retry: 1,
    },
  },
});

function App() {
  const [user, setUser] = useState(null);
  const [authLoading, setAuthLoading] = useState(true);

  //    JWT 
  useEffect(() => {
    const checkSession = async () => {
      //    
      if (!apiService.isAuthenticated()) {
        setAuthLoading(false);
        return;
      }

      try {
        //    API
        const response = await apiService.checkUser();
        if (response.success && response.user) {
          setUser(response.user);
        } else {
          //   - 
          apiService.clearToken();
        }
      } catch (err) {
        console.error('  :', err);
        //     - 
        apiService.clearToken();
      }
      
      setAuthLoading(false);
    };
    
    checkSession();
  }, []);

  //    Telegram Login Widget
  const handleTelegramAuth = useCallback(async (telegramUser) => {
    try {
      const response = await apiService.authTelegram(telegramUser);
      if (response.success && response.user) {
        setUser(response.user);
        //     
        queryClient.invalidateQueries();
      }
    } catch (err) {
      console.error(' :', err);
      alert('   Telegram');
    }
  }, []);

  //  callback  Telegram Widget
  useEffect(() => {
    window.onTelegramAuth = handleTelegramAuth;
    return () => { window.onTelegramAuth = null; };
  }, [handleTelegramAuth]);

  const handleLogout = () => {
    setUser(null);
    apiService.clearToken();
    //    
    queryClient.clear();
  };

  // Telegram Login Widget   
  const TelegramWidget = () => {
    const ref = React.useRef(null);

    useEffect(() => {
      if (!BOT_USERNAME || !ref.current) return;
      ref.current.innerHTML = '';
      const script = document.createElement('script');
      script.src = 'https://telegram.org/js/telegram-widget.js?22';
      script.setAttribute('data-telegram-login', BOT_USERNAME);
      script.setAttribute('data-size', 'medium');
      script.setAttribute('data-radius', '8');
      script.setAttribute('data-onauth', 'onTelegramAuth(user)');
      script.setAttribute('data-request-access', 'write');
      script.async = true;
      ref.current.appendChild(script);
    }, []);

    return <div ref={ref} />;
  };

  if (authLoading) return null;

  return (
    <QueryClientProvider client={queryClient}>
      <Router basename="/GotIt">
        <BackgroundGradient />
        <div className="app">
          <header className="header">
            <div className="container">
              <h1 className="logo" href="https://bzden4ik.github.io/GotIt">GotIt</h1>
              <nav className="nav">
                <Link to="/" className="nav-link"></Link>
                <Link to="/tracked" className="nav-link"></Link>
              </nav>
              <div className="user-section">
                {user ? (
                  <div className="user-logged">
                    <span className="user-info">
                      {user.username ? `@${user.username}` : user.firstName}
                    </span>
                    <button className="logout-btn" onClick={handleLogout}></button>
                  </div>
                ) : (
                  <TelegramWidget />
                )}
              </div>
            </div>
          </header>

          <main className="main">
            <Routes>
              <Route path="/" element={<SearchPage user={user} />} />
              <Route path="/tracked" element={<TrackedList user={user} />} />
            </Routes>
          </main>
        </div>
      </Router>
      
      {/* React Query DevTools (  dev) */}
      {process.env.NODE_ENV === 'development' && (
        <ReactQueryDevtools initialIsOpen={false} />
      )}
    </QueryClientProvider>
  );
}

export default App;
